@Injectable({
  providedIn: 'root',
})
export class AppServiceService {
  teacher: BehaviorSubject<Teacher>;
  assessment: BehaviorSubject<any>;
  constructor() {}
  getUser(): User {
    const user: User = JSON.parse(
      localStorage.getItem(environment.authTokenKey)
    );
    return user;
  }
  getUserStateAccess(): string {
    const user: User = JSON.parse(
      localStorage.getItem(environment.authTokenKey)
    );
    return user.state_access;
  }
  getStates(state_access = 'All'): IState[] {
    let states: IState[] = [];
    if (state_access === 'All') {
      states = locations.map((item) => item.state);
    } else {
      states = locations
        .filter(
          (item) =>
            item.state.name.toLowerCase().trim() ===
            state_access.toLowerCase().trim()
        )
        .map((item) => item.state);
    }

    return states;
  }
  subtractFromCurrentDate(days: number) {
    var date = new Date();
    date.setDate(date.getDate() - days);
    return date;
  }
  addDaysToCurrentDate(days: number) {
    var date = new Date();
    date.setDate(date.getDate() + days);
    return date;
  }
  formatDate(dateObj: Date) {
    const date =
      dateObj.getDate() < 10 ? `0${dateObj.getDate()}` : `${dateObj.getDate()}`;
    const month =
      1 + dateObj.getMonth() < 10
        ? `0${1 + dateObj.getMonth()}`
        : `${1 + dateObj.getMonth()}`;
    const year = dateObj.getFullYear();
    return `${year}-${month}-${date}`;
  }
  getLocalGovernments(states: string[] = []): ILocalGovernments[] {
    const locals: ILocalGovernments[] = [];
    let filteredStates: IState[] = [];
    if (states.length === 0) {
      const state_access = this.getUserStateAccess();
      filteredStates =
        state_access.toLowerCase() === 'all'
          ? locations.map((item) => item.state)
          : locations
              .filter((item) => [state_access].includes(item.state.name))
              .map((item) => item.state);
    } else {
      filteredStates = locations
        .filter((item) => states.includes(item.state.name))
        .map((item) => item.state);
    }

    filteredStates.forEach((item: any) => locals.push(...item.locals));
    console.log('locals: ', locals);
    return locals.sort((item1, item2) => {
      if (item1.name.toLowerCase() > item2.name.toLowerCase()) return 1;
      return -1;
    });
  }
  showPopAlertError(title?: string, message?: string) {
    Swal.fire({
      icon: 'error',
      title: title ? title : 'Operation failed',
      text: message
        ? message
        : 'Some error were encountered our technical team will get on it quickly.',
      showClass: {
        popup: 'animate__animated animate__fadeInDown',
      },
      hideClass: {
        popup: 'animate__animated animate__fadeOutUp',
      },
    });
  }

  showPopAlertSuccess(title?: string, message?: string) {
    Swal.fire({
      icon: 'success',
      title: title ? title : 'Operation failed',
      text: message ? message : 'Operation successful',
      showClass: {
        popup: 'animate__animated animate__fadeInDown',
      },
      hideClass: {
        popup: 'animate__animated animate__fadeOutUp',
      },
    });
  }

  // showPopAlertHttpError(error: any) {
  //   Swal.fire({
  //     icon: 'error',
  //     title: 'Operation failed',
  //     text: error?.error?.message
  //       ? error?.error?.message
  //       : 'Some errors were encountered, our technical team will resolve it shortly',
  //     showClass: {
  //       popup: 'animate__animated animate__fadeInDown',
  //     },
  //     hideClass: {
  //       popup: 'animate__animated animate__fadeOutUp',
  //     },
  //   });
  // }
}

